#app:before{
    position: absolute;
    display: block;
    width: 100%;
    z-index: -1;
    content: '';
    min-height: 500px;
    -webkit-filter: blur(1px);
            filter: blur(1px);
    background: url(/static/img/bg.465adb6.jpg) no-repeat;
    background-size: 100% auto;
    -webkit-animation: bgmove 30s linear infinite;
            animation: bgmove 30s linear infinite;
}
@-webkit-keyframes bgmove {
0%{
     -webkit-transform: translateX(-70px);
             transform: translateX(-70px);
}
50%{
     -webkit-transform: translateX(70px);
             transform: translateX(70px);
}
100%{
     -webkit-transform: translateX(-70px);
             transform: translateX(-70px);
}
}
@keyframes bgmove {
0%{
     -webkit-transform: translateX(-70px);
             transform: translateX(-70px);
}
50%{
     -webkit-transform: translateX(70px);
             transform: translateX(70px);
}
100%{
     -webkit-transform: translateX(-70px);
             transform: translateX(-70px);
}
}
.clear:after {
  display: block;
  content: '';
  clear: both;
}

